import * as __SNOWPACK_ENV__ from './_snowpack/env.js';

import { createElement as e } from './_snowpack/pkg/react.js';
import ReactDOM from './_snowpack/pkg/react-dom.js';

import {
  BaseApp,
  makeAppConfig
} from './_snowpack/pkg/react-aws-cognito-lambda-dynamodb-base-prototype-app.js';

import Home from './Home.js';

const myAppMessages = {
  LOGIN_SUCCESSFUL: 'Usuário logado com sucesso',
  LOGIN_FAILED: 'Não foi possível completar o login',
  LOGOUT_SUCCESSFUL: 'Usuário deslogado com sucesso',
  LOGOUT_FAILED: 'Não foi possível deslogar o usuário',
  LOGGED_OUT_USER: 'Usuário não logado',
  LOG_COULD_NOT_LOGIN_WITH_REFRESHED_TOKENS: 'Could not login with refreshed tokens',
  LOG_COULD_NOT_DECODE_AUTHENTICATION_RESPONSE: 'Could not decode authentication response',
  LOG_COULD_NOT_GET_REFRESHED_TOKENS: 'Could not get refreshed tokens',
  LOG_NO_REFRESH_TOKEN_AVAILABLE: 'No refresh token available',
  LOG_COULD_NOT_REFRESH_TOKENS: 'Could not refresh tokens',
  LOG_COULD_NOT_GET_IDENTIFICATION_TOKENS: 'Could not get identification tokens with this authorization code'
};

const myEnv = {
  appHost: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_APP_HOST,
  appBasePath: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_APP_BASE_PATH,
  appLogoUrl: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_APP_LOGO_URL,
  appRegion: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_AWS_REGION,
  appUserPoolId: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_USER_POOL_ID,
  appUserPoolDomain: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_USER_POOL_DOMAIN,
  appClientId: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_USER_POOL_APP_CLIENT_ID,
  appIdentityPoolId: __SNOWPACK_ENV__.SNOWPACK_PUBLIC_IDENTITY_POOL_ID
};

const appConfig = makeAppConfig({
  ...myEnv,
  appRefreshTokenStorageKey: 'appiscina-refresh-token',
  appMessages: myAppMessages,
  hideLogin: true
});

const appRoutes = [
  {
    name: 'home',
    label: 'Home',
    path: '/',
    hideFromMenu: false,
    component: Home,
    options: { exact: true }
  }
];

const App = () => (
  e(BaseApp, { appRoutes, appConfig })
);

ReactDOM.render(
  e(App, null, null),
  document.getElementById('app_container')
);
