import { useCallback } from './_snowpack/pkg/react.js';

import { useDDB } from './_snowpack/pkg/react-aws-cognito-lambda-dynamodb-base-prototype-app.js';

const useDDBQueries = () => {
  const { documentDB, ddbPut } = useDDB();

  const addToWaitlist = useCallback((email) => {
    const params = {
      TableName: 'Waitlist',
      Item: {
        email
      }
    };
    return ddbPut(params);
  }, [ddbPut]);

  return {
    addToWaitlist: documentDB && addToWaitlist
  };
};

export default useDDBQueries;
