import {
  createElement as e,
  useCallback,
  useEffect,
  useState
} from './_snowpack/pkg/react.js';
import {
  Button,
  Container,
  Grid,
  Typography
} from './_snowpack/pkg/@material-ui/core.js';

import {
  useAppConfig,
  useUser
} from './_snowpack/pkg/react-aws-cognito-lambda-dynamodb-base-prototype-app.js';

import WaitlistDialog from './WaitlistDialog.js';

const Home = () => {
  const { appConfig: { appLogoUrl } } = useAppConfig();
  const { awsConfig, loginAnonymously } = useUser();

  const [waitlistDialogOpen, setWaitlistDialogOpen] = useState(false);

  const dismissWaitlistDialog = useCallback(() => {
    setWaitlistDialogOpen(false);
  }, []);

  useEffect(() => {
    if (!awsConfig) {
      loginAnonymously();
    }
  }, [awsConfig, loginAnonymously]);

  return e(Container, null,
    e(Grid,
      {
        container: true,
        justify: 'center',
        spacing: 2,
        alignItems: 'center'
      },
      e(Grid, { item: true },
        e('img',
          {
            src: appLogoUrl
          },
          null)),
      e(Grid, { item: true, xs: 12, sm: 8 },
        e(Grid, { container: true, justify: '', spacing: 2 },
          e(Grid, { item: true, xs: 12 },
            e(Typography, { variant: 'h6' }, 'APPiscina')),
          e(Grid, { item: true, xs: 12 },
            e(Typography, { variant: 'subtitle1' }, 'Aplicativo de economia colaborativa de compartilhamento de piscinas privadas')),
          e(Grid, { item: true, xs: 12 },
            e(Grid, { container: true, spacing: 1, alignItems: 'center' },
              e(Grid, { item: true },
                e(Button,
                  {
                    variant: 'contained',
                    color: 'primary',
                    onClick: () => setWaitlistDialogOpen(true)
                  },
                  'Ingressar na lista VIP')),
              e(Grid, { item: true },
                e(Typography,
                  { variant: 'body2' },
                  'Seja um dos primeiros e torne-se um Piscineiro Fundador!'))))))),
    waitlistDialogOpen
      ? e(WaitlistDialog, { dismissWaitlistDialog })
      : null);
};

export default Home;
