import { createElement as e, useCallback } from './_snowpack/pkg/react.js';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField
} from './_snowpack/pkg/@material-ui/core.js';

import {
  useMessage
} from './_snowpack/pkg/react-aws-cognito-lambda-dynamodb-base-prototype-app.js';

import useDDBQueries from './ddbQueries.js';

const WaitlistDialog = ({ dismissWaitlistDialog }) => {
  const { showMessage } = useMessage();
  const { addToWaitlist } = useDDBQueries();

  const handleSubmit = useCallback((event) => {
    event.preventDefault();

    const email = event.target.email.value;

    addToWaitlist(email).then(() => {
      showMessage('Parabéns, você está na Lista VIP! Aguarde mais informações por email em breve.');
    }).catch((err) => {
      showMessage('Infelizmente ocorreu um erro ao tentar adicionar o seu email à Lista VIP. Tente novamente em breve.');
      console.error(`Erro ao tentar adicionar o email ${email} à Lista VIP.`, err);
    }).finally(() => {
      dismissWaitlistDialog();
    });
  }, [addToWaitlist, showMessage, dismissWaitlistDialog]);

  return e(Dialog,
    {
      open: true,
      onClose: dismissWaitlistDialog
    },
    e('form', { onSubmit: handleSubmit },
      e(DialogTitle, {}, 'Seja um dos primeiros!'),
      e(DialogContent, {},
        e(DialogContentText, {}, 'Ingresse na Lista VIP e indique seus amigos para obter o status de Piscineiro Fundador com benefícios vitalícios'),
        e(TextField, {
          variant: 'outlined',
          fullWidth: true,
          name: 'email',
          type: 'email',
          label: 'Meu melhor email',
          required: true
        }, 'Seu melhor email')),
      e(DialogActions, {},
        e(Button,
          {
            variant: 'contained',
            onClick: dismissWaitlistDialog
          },
          'Deixar para depois'),
        e(Button,
          {
            variant: 'contained',
            color: 'primary',
            type: 'submit'
          },
          'Ingressar já'))));
};

export default WaitlistDialog;
